import React, { useState } from "react";
import LeaveApplication from './leaveApplication/LeaveApplication.js'
import CorrectionApplication from "./attendanceCorrectionApplication/CorrectionApplication.js";
import TourApplication from "./tourApplication/TourApplication.js";
import AttendanceLogs from "./attendanceLogsApplication/AttendanceLogs";
import LeaveToday from "./LeaveToday.js";
import { isPermission } from "../../utils/common.js";

const LeaveApplicationCards = (props) => {
    const {
        allLeaveTypeList,

    } = props
    const [openModel, setOpenModel] = useState(false)
    const [tourModel, setTourModel] = useState(false)
    const [isShowAllBreak, setIsShowAllBreak] = useState(false)
    const [isShowCorrectionModel, setIsShowCorrectionMode] = useState(false)

    const openLeaveApplicationModel = () => {
        setOpenModel(!openModel)
    }
    const openTourApplicationModel = () => {
        setTourModel(!tourModel)
    }

    /* Apply Attendance Correction  */
    const toggleCorrectionApplicationModal = () => {
        setIsShowCorrectionMode(!isShowCorrectionModel)
    }
    const toggleShowAllBreakModal = () => {
        setIsShowAllBreak(!isShowAllBreak)
        setIsShowCorrectionMode(!isShowCorrectionModel)
    }




    return (
        <>
            <div className="row leavesMain mb-3">
                <div className="col-md-6 pe-md-2">
                    <div className="cardBox leaveAppli h-100">
                        <div className="row">
                            <div className="col-4">
                                <div className="d-flex justify-content-between align-items-start h-100 flex-column">
                                    <div className="cardHead">
                                        <h3>
                                            Leave <span>Application</span>
                                        </h3>
                                    </div>
                                    <div className="position-relative">
                                        <div className="calenderimg">
                                            <img src="images/calenderimg.svg" alt="" />
                                        </div>
                                        <lottie-player
                                            src="images/Calendar.json"
                                            style={{ width: "70px", height: "70px" }}
                                            loop
                                            autoPlay
                                        />
                                    </div>
                                </div>
                            </div>
                            <div className="col-8 selectModal position-relative z-1">
                                <div className="row px-md-1 ">
                                    {isPermission("apply_leave")&&<div className="col-md-6 px-md-2 mb-3">
                                        <span
                                            className="totalBx h-100"
                                            onClick={() => openLeaveApplicationModel()}
                                        >
                                            <figure>
                                                <img src="images/applyleavesIcon.svg" alt="" width={50} />
                                            </figure>
                                            <h3 className="fw-bolder my-0">Apply Leave</h3>
                                            <img
                                                src="images/firstlineBlue.svg"
                                                className="cornerLine"
                                                alt=""
                                                width={95}
                                            />
                                        </span>
                                    </div>}
                                   {isPermission("apply_tour") &&<div className="col-md-6 px-md-2 mb-3">
                                        <span
                                            className="totalBx h-100"
                                            onClick={() => openTourApplicationModel()}
                                        >
                                            <figure>
                                                <img src="images/applytoursIcon.svg" alt="" width={50} />
                                            </figure>
                                            <h3 className="fw-bolder my-0">Apply Tour</h3>
                                            <img
                                                src="images/secondlineBlue.svg"
                                                className="cornerLine"
                                                alt=""
                                                width={80}
                                            />
                                        </span>
                                    </div>}
                                    {isPermission("apply_attendance_correct") &&<div className="col-md-6 px-md-2 mb-3">
                                        <span
                                            // data-bs-toggle="offcanvas"
                                            className="totalBx h-100"
                                            onClick={() => toggleCorrectionApplicationModal()}
                                        >
                                            <figure>
                                                <img
                                                    src="images/attendancecorrIcon.svg"
                                                    alt=""
                                                    width={50}
                                                />
                                            </figure>
                                            <h3 className="fw-bolder my-0">Attendance Correction</h3>
                                            <img
                                                src="images/firstlineBlue.svg"
                                                className="cornerLine"
                                                alt=""
                                                width={80}
                                            />
                                        </span>
                                    </div>}
                                    {isPermission("attendance_logs") &&<div className="col-md-6 px-md-2 mb-3">
                                        <span

                                            // data-bs-toggle="modal"
                                            className="totalBx h-100"
                                        >
                                            <figure>
                                                <img
                                                    src="images/attendancelogIcon.svg"
                                                    alt=""
                                                    width={50}
                                                />
                                            </figure>
                                            <h3 className="fw-bolder my-0">Attendance Logs </h3>
                                            <img
                                                src="images/secondlineBlue.svg"
                                                className="cornerLine"
                                                alt=""
                                                width={80}
                                            />
                                        </span>
                                    </div>}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="col-md-6 ps-md-2">
                    <LeaveToday
                    />
                </div>
            </div>
            <LeaveApplication
                openModel={openModel}
                openLeaveApplicationModel={openLeaveApplicationModel}
                allLeaveTypeList={allLeaveTypeList}

            />

            { /* Correction modal */}
            {isShowCorrectionModel && <CorrectionApplication
                handleOpenClose={toggleCorrectionApplicationModal}
                openModel={isShowCorrectionModel}
                toggleShowAllBreakModal={toggleShowAllBreakModal}
            />}
            {tourModel && <TourApplication
                openTourApplicationModel={openTourApplicationModel}
                isOpenModal={tourModel}
            />}

            <AttendanceLogs
                handleOpenClose={toggleShowAllBreakModal}
                openModel={isShowAllBreak}
            />
        </>
    )
}

export default LeaveApplicationCards
