import React, { useState, useEffect, useMemo } from "react";
import TableName from "../reuseableComponents/customTable/TableName";
import CustomTable from "../reuseableComponents/customTable/CustomTable"
import TableTabs from "./TableTabs";
import Image from '../commanComponent/Image'
import ApproveRejectModal from "../modal/ApproveRejectModal";
import { useDispatch, useSelector } from 'react-redux'
import { leaveAction } from '../../core/resource/actions';
import { activeTabPermission, dateFormate, isPermission, setStatusColor } from "../../utils/common";

const ApplicationRequest = (props) => {
    const {
        getAttendanceCorrectionRequestDataPending,
    } = useSelector(state => state.attendanceCorrection)
    const { getEmpAppliedLeavePending } = useSelector(state => state.leave);
    const {
        tabletabs,
        tableName,
        getEmployeeLeaveRequest,
        setCurrentPage,
        currentPage,
        getEmployeeAppliedLeave,
        attendanceCorrectionAppliedData,
        onHandleProgressModel,
        setAppliedStatus,
        setAppliedType,
        compOffData,
        tourData,
        activeRequestTab
    } = props
    const dispatch = useDispatch()
    const {
        cancelLeavePending,
        cancelLeaveRes,
        cancelLeaveError
    } = useSelector(state => state.leave)
    const [openCancelModel, setOpenCancelModel] = useState(false);
    const [cancelLeaveData, setCancelLeaveData] = useState();


    useEffect(() => {
        if (!cancelLeavePending) {
            if (cancelLeaveError) {
                return;
            }
            if (cancelLeaveRes) {
                if (cancelLeaveRes?.code === 200) {
                    dispatch(leaveAction.handleClearLeaveStore("HANDLE_CANCEL_LEAVE_STORE"))
                    // setStatus("pending")
                    setOpenCancelModel(!openCancelModel)
                }
            }
        }
    }, [
        cancelLeaveRes,
        cancelLeaveError,
        cancelLeavePending
    ]);

    const leaveDataFields = [
        { key: 'sn', label: 'S.N' },
        { key: 'emp_id', label: 'Emp ID' },
        { key: 'first_name', label: 'First Name' },
        { key: 'leave_date', label: 'Leave Date' },
        { key: 'leave_short_name', label: 'Leave Type' },
        { key: 'leave_status', label: 'Leave status' },
        { key: 'reason', label: 'Reason' },
        { key: 'created_at', label: 'Created At' },
        { key: 'action', label: 'Action' },

    ];
    const scopedSlotsApprove = useMemo(() => ({
        sn: (_, index) => (
            <span>{index + 1}</span>
        ),
        created_at: item => dateFormate(item?.created_at),
        action: item => (
            <div className="button-container">
                {item?.is_cancel == false && item?.approvers_status == "pending" &&
                    <span
                        style={{ cursor: "pointer" }}
                        onClick={() => onHandleCancelLeave(item)}>
                        <Image imgSrc={"images/rejectIcon.svg"} altText={""} width={32}></Image>
                    </span>}
                <span style={{ cursor: "pointer" }}
                    onClick={() => onHandleProgressModel("leave", item?._id)}>

                    <Image imgSrc={"images/info-orange-bg.svg"} altText={""} width={32}></Image>
                </span>
            </div>
        ),
    }), [getEmployeeLeaveRequest]);

    const onHandleCancelLeave = (item) => {
        setOpenCancelModel(!openCancelModel)
        setCancelLeaveData(item)
    }

    const onCancelLeave = (type) => {
        if (type) {
            let data = {
                cancel_id: cancelLeaveData?._id,
                employee_id: cancelLeaveData?.employee_id,
            }
            dispatch(leaveAction.cancelLeaveRequest(data))
        } else {
            setOpenCancelModel(!openCancelModel)
        }
    }
    const onHandleAppliedStatus = (requestType, type) => {
        setAppliedType(requestType)
        setAppliedStatus(type)
        if (currentPage === 1) {
            getEmployeeAppliedLeave(requestType, type)
        } else {
            setCurrentPage(1)
        }
    }

    /* Attendance Correction */
    const attendanceCorrectionFields = [
        { key: 'sn', label: 'S.N' },
        { key: 'date', label: 'Correction For' },
        { key: 'type', label: 'Correction Type' },
        { key: 'correction_check_in_time', label: 'Check In Time' },
        { key: 'correction_check_out_time', label: 'Check Out Time' },
        { key: 'reason', label: 'Reason' },
        { key: 'approvers_status', label: 'Approve For' },
        { key: 'created_at', label: 'Created At' },
        { key: 'action', label: 'Action' }
    ];

    const attendanceCorrectionScopedSlots = useMemo(() => ({
        sn: (_, index) => (
            <span>{index + 1}</span>
        ),
        date: (item) => (
            <span>{dateFormate(item?.date)}</span>
        ),
        approvers_status: (item, index) => (
            <span
                className={`${setStatusColor(item?.approvers_status)} text-capitalize`}
            >
                {item?.approvers_status}
            </span>
        ),
        created_at: item => dateFormate(item?.created_at),
        action: item => (
            <div className="button-container">
                <span style={{ cursor: "pointer" }}
                    onClick={() => onHandleProgressModel("attendance_correction", item?._id)}>

                    <Image imgSrc={"images/info-orange-bg.svg"} altText={""} width={32}></Image>
                </span>
            </div>
        ),

    }), []);



    /* Comp Off */
    const compOffFields = [
        { key: 'sn', label: 'S.N' },
        { key: 'comp_off_Date', label: 'Comp Off Date' },
        { key: 'comp_off_status', label: 'Comp Off Status' },
        { key: 'created_at', label: 'Created Date' },
        { key: 'action', label: 'Action' },

    ];
    const compOffScopedSlots = useMemo(() => ({
        sn: (_, index) => (
            <span>{index + 1}</span>
        ),
        created_at: (item, index) => (
            <span>{dateFormate(item?.created_at)}</span>
        ),
        action: item => (
            <div className="button-container">
                {item?.is_cancel == false && item?.approvers_status == "pending" &&
                    <span
                        style={{ cursor: "pointer" }}
                        onClick={() => onHandleCancelLeave(item)}>
                        <Image imgSrc={"images/rejectIcon.svg"} altText={""} width={32}></Image>
                    </span>}
                <span style={{ cursor: "pointer" }}
                    onClick={() => onHandleProgressModel("leave", item?._id)}>

                    <Image imgSrc={"images/info-orange-bg.svg"} altText={""} width={32}></Image>
                </span>
            </div>
        ),
    }), [compOffData]);


    /* Tour */

    const tourFields = [
        { key: 'sn', label: 'S.N' },
        { key: 'emp_id', label: 'Employee ID' },
        { key: 'name', label: 'Employee Name' },
        { key: 'from_date', label: 'From Date' },
        { key: 'to_date', label: 'To Date' },
        { key: 'tour_to', label: 'Tour To' },
        { key: 'reason', label: 'Reason' },
        { key: 'created_at', label: 'Created Date' },
        { key: 'action', label: 'Action' }
    ];
    const tourScopedSlots = useMemo(() => ({
        sn: (_, index) => (
            <span className='srNum'>{index + 1}</span>
        ),
        action: item => (
            <div className="button-container">
                {item?.is_cancel == false && item?.approvers_status == "pending" &&
                    <span
                        style={{ cursor: "pointer" }}
                        onClick={() => onHandleCancelLeave(item)}>
                        <Image imgSrc={"images/rejectIcon.svg"} altText={""} width={32}></Image>
                    </span>}
                <span style={{ cursor: "pointer" }}
                    onClick={() => onHandleProgressModel("leave", item?._id)}>

                    <Image imgSrc={"images/info-orange-bg.svg"} altText={""} width={32}></Image>
                </span>
            </div>
        ),
    }), [tourData]);


    return (
        <div className="cardBox myRequest mb-3">
            <TableName tableName={tableName} />
            <TableTabs tabletabs={tabletabs} type="request" />
            <div className="tab-content">
                {isPermission("attendance_correction_request") && <div className={`tab-pane fade 
                    ${activeRequestTab === "attendance_correction" ? "active show" : ""}
                    `} id="attendance_correction">
                    <div className="requestdata">
                        <div className="d-flex align-items-center mb-4">
                            {/* <span className="fw-bold fs-6 text-black">08 Request</span> */}
                            <ul className="nav nav-tabs leaveTabs ms-3">
                                <li className="nav-item">
                                    <button className="nav-link active" data-bs-toggle="tab" data-bs-target="#pending"
                                        onClick={() => onHandleAppliedStatus("attendance_correction", "pending")}
                                        type="button">Pending
                                        {/* <span>4</span> */}
                                    </button>
                                </li>
                                <li className="nav-item">
                                    <button className="nav-link" data-bs-toggle="tab" data-bs-target="#pending"
                                        type="button"
                                        onClick={() => onHandleAppliedStatus("attendance_correction", "approve")}
                                    >Approved
                                        {/* <span>4</span> */}
                                    </button>
                                </li>
                                <li className="nav-item">
                                    <button className="nav-link" data-bs-toggle="tab" data-bs-target="#pending"
                                        type="button"
                                        onClick={() => onHandleAppliedStatus("attendance_correction", "reject")}
                                    >Rejected
                                        {/* <span>0</span> */}
                                    </button>
                                </li>
                            </ul>
                        </div>
                        <div className="tab-content">
                            <div className="tab-pane fade active show" id="pending">

                                <CustomTable
                                    data={attendanceCorrectionAppliedData?.data}
                                    fields={attendanceCorrectionFields}
                                    isLoading={getAttendanceCorrectionRequestDataPending}
                                    pagination={true}
                                    currentPage={currentPage}
                                    setCurrentPage={setCurrentPage}
                                    totalPages={attendanceCorrectionAppliedData?.totalPages}
                                    isHeader={false}
                                    tableStyle={{ padding: "0px", boxShadow: "none" }}
                                    scopedSlots={attendanceCorrectionScopedSlots}
                                />

                            </div>

                        </div>
                    </div>
                </div>}

                {isPermission("leave_request") && <div className={`tab-pane fade
                    ${activeRequestTab === "leave" ? "active show" : ""}
                    `} id="leave">
                    <div className="requestdata">
                        <div className="d-flex align-items-center mb-4">
                            {/* <span className="fw-bold fs-6 text-black">12 Request</span> */}
                            <ul className="nav nav-tabs leaveTabs ms-3">
                                <li className="nav-item">
                                    <button
                                        className="nav-link active"
                                        data-bs-toggle="tab"
                                        data-bs-target="#pendingleave"
                                        type="button"
                                        onClick={() => onHandleAppliedStatus("leave", "pending")}
                                    >
                                        Pending
                                        {/* <span>4</span> */}
                                    </button>
                                </li>
                                <li className="nav-item">
                                    <button
                                        className="nav-link"
                                        data-bs-toggle="tab"
                                        data-bs-target="#pendingleave"
                                        type="button"
                                        onClick={() => onHandleAppliedStatus("leave", "approve")}
                                    >
                                        Approved
                                        {/* <span>4</span> */}
                                    </button>
                                </li>
                                <li className="nav-item">
                                    <button
                                        className="nav-link"
                                        data-bs-toggle="tab"
                                        data-bs-target="#reject"
                                        type="button"
                                        onClick={() => onHandleAppliedStatus("leave", "reject")}
                                    >
                                        Rejected
                                        {/* <span>0</span> */}
                                    </button>
                                </li>
                                <li className="nav-item">
                                    <button
                                        className="nav-link"
                                        data-bs-toggle="tab"
                                        data-bs-target="#pendingleave"
                                        type="button"
                                        onClick={() => onHandleAppliedStatus("leave", "cancelled")}
                                    >
                                        Cancelled
                                        {/* <span>4</span> */}
                                    </button>
                                </li>
                            </ul>
                        </div>
                        <div className="tab-content">
                            <div className="tab-pane fade active show" id="pendingleave">
                                <CustomTable
                                    data={getEmployeeLeaveRequest?.data}
                                    fields={leaveDataFields}
                                    isLoading={getEmpAppliedLeavePending}
                                    pagination={true}
                                    currentPage={currentPage}
                                    setCurrentPage={setCurrentPage}
                                    totalPages={getEmployeeLeaveRequest?.totalPages}
                                    isHeader={false}
                                    tableStyle={{ padding: "0px", boxShadow: "none" }}
                                    scopedSlots={scopedSlotsApprove}
                                />
                            </div>

                        </div>
                    </div>
                </div>}
                {isPermission("comp_off_request") && <div className={`tab-pane fade ${activeRequestTab === "comp_off" ? "active show" : ""}`} id="comp_off">
                    <div className="requestdata">
                        <div className="d-flex align-items-center mb-4">
                            {/* <span className="fw-bold fs-6 text-black">08 Request</span> */}
                            <ul className="nav nav-tabs leaveTabs ms-3">
                                <li className="nav-item">
                                    <button
                                        className="nav-link active"
                                        data-bs-toggle="tab"
                                        data-bs-target="#pendingCompOff"
                                        type="button"
                                        onClick={() => onHandleAppliedStatus("comp_off", "pending")}
                                    >
                                        Pending
                                        {/* <span>4</span> */}
                                    </button>
                                </li>
                                <li className="nav-item">
                                    <button
                                        className="nav-link"
                                        data-bs-toggle="tab"
                                        data-bs-target="#pendingCompOff"
                                        type="button"
                                        onClick={() => onHandleAppliedStatus("comp_off", "approve")}
                                    >
                                        Approved
                                    </button>
                                </li>
                                <li className="nav-item">
                                    <button
                                        className="nav-link"
                                        data-bs-toggle="tab"
                                        data-bs-target="#pendingCompOff"
                                        type="button"
                                        onClick={() => onHandleAppliedStatus("comp_off", "reject")}
                                    >
                                        Rejected
                                    </button>
                                </li>

                            </ul>
                        </div>
                        <div className="tab-content">
                            <div className="tab-pane fade active show" id="pendingCompOff">
                                <div className="table-responsive tableDiv">
                                    <CustomTable
                                        data={compOffData?.data}
                                        fields={compOffFields}
                                        isLoading={getEmpAppliedLeavePending}
                                        pagination={true}
                                        currentPage={currentPage}
                                        setCurrentPage={setCurrentPage}
                                        totalPages={compOffData?.totalPages}
                                        isHeader={false}
                                        tableStyle={{ padding: "0px", boxShadow: "none" }}
                                        scopedSlots={compOffScopedSlots}
                                    />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>}
                {isPermission("tour_request") && <div className={`tab-pane fade ${activeRequestTab === "tour" ? "active show" : ""}`} id="tour">
                    <div className="requestdata">
                        <div className="d-flex align-items-center mb-4">
                            {/* <span className="fw-bold fs-6 text-black">08 Request</span> */}
                            <ul className="nav nav-tabs leaveTabs ms-3">
                                <li className="nav-item">
                                    <button
                                        className="nav-link active"
                                        data-bs-toggle="tab"
                                        data-bs-target="#pendingTour"
                                        type="button"
                                        onClick={() => onHandleAppliedStatus("tour", "pending")}
                                    >
                                        Pending
                                        {/* <span>4</span> */}
                                    </button>
                                </li>
                                <li className="nav-item">
                                    <button
                                        className="nav-link"
                                        data-bs-toggle="tab"
                                        data-bs-target="#pendingTour"
                                        type="button"
                                        onClick={() => onHandleAppliedStatus("tour", "approve")}
                                    >
                                        Approved
                                    </button>
                                </li>
                                <li className="nav-item">
                                    <button
                                        className="nav-link"
                                        data-bs-toggle="tab"
                                        data-bs-target="#pendingTour"
                                        type="button"
                                        onClick={() => onHandleAppliedStatus("tour", "reject")}
                                    >
                                        Rejected
                                    </button>
                                </li>

                            </ul>
                        </div>
                        <div className="tab-content">
                            <div className="tab-pane fade active show" id="pendingTour">
                                <div className="table-responsive tableDiv">
                                    <CustomTable
                                        data={tourData?.data}
                                        fields={tourFields}
                                        isLoading={getEmpAppliedLeavePending}
                                        pagination={true}
                                        currentPage={currentPage}
                                        setCurrentPage={setCurrentPage}
                                        totalPages={tourData?.totalPages}
                                        isHeader={false}
                                        tableStyle={{ padding: "0px", boxShadow: "none" }}
                                        scopedSlots={tourScopedSlots}
                                    />
                                </div>
                            </div>


                        </div>
                    </div>
                </div>}
            </div>
            {openCancelModel && (
                <ApproveRejectModal
                    onApproveReject={onCancelLeave}
                    isShow={openCancelModel}
                    desc={`You want to this Cancel this leave `}
                    isLoading={cancelLeavePending}
                />)
            }
        </div>
    )
}

export default ApplicationRequest
