import React, { useEffect, useRef, useState } from "react";
import LeaveCountHeader from "./LeaveCountHeader";
import { useDispatch, useSelector } from 'react-redux'
import { attendanceCorrectionActions, commonActions, leaveAction } from '../../core/resource/actions'
import ApplicationRequest from "./ApplicationRequest";
import ApplicationApproval from "./ApplicationApproval";
import LeaveApplicationCards from "./LeaveApplicationCards";
import ProgressModel from "./ProgressModel";
import { pagination } from "../../utils/Constants.config";
import { convertToTS } from "../../utils/AppHelper";
import { activeTabPermission, isPermission } from "../../utils/common";


const LeaveManagement = () => {
  const dispatch = useDispatch()

  const [allLeaveTypeList, setAllLeaveTypeList] = useState([])
  const [currentPage, setCurrentPage] = useState(1);
  const [getEmployeeLeaveRequest, setEmployeeLeaveRequest] = useState([])
  const [getEmployeeLeaveApprovel, setEmployeeLeaveApprovel] = useState([])
  const [getEmployeeLeaveApproval, setEmployeeLeaveApproval] = useState([])
  const [attendanceCorrectionAppliedData, setAttendanceCorrectionAppliedData] = useState('')
  const [attendanceCorrectionApprovalData, setAttendanceCorrectionApprovalData] = useState('')
  const [appConstantData, setAppConstantData] = useState('')
  const [openProgressModel, setOpenProgressModel] = useState(false)
  const [progressData, setProgressData] = useState([])
  const [approvalCurrentPage, setApprovalCurrentPage] = useState(1)
  const [approvalType, setApprovalType] = useState('')
  const [approvalStatus, setApprovalStatus] = useState('pending')
  const [processType, setProcessType] = useState('')
  const [appliedCurrentPage, setAppliedCurrentPage] = useState(1)
  const [appliedType, setAppliedType] = useState('')
  const [appliedStatus, setAppliedStatus] = useState('pending')
  const [compOffData, setCompOffData] = useState('')
  const [tourData, setTourData] = useState('')
  const isApprovalFirstNotRender = useRef(true);
  
  const {
    getLeaveTypeListPending,
    getLeaveTypeListRes,
    getLeaveTypeListError,

    getEmpAppliedLeavePending,
    getEmpAppliedLeaveRes,
    getEmpAppliedLeaveError,

    getEmpApprovelLeavePending,
    getEmpApprovelLeaveRes,
    getEmpApprovelLeaveError,

    getLeaveProgressPending,
    getLeaveProgressRes,
    getLeaveProgressError,

    getCompOffDataPending,
    getCompOffDataRes,
    getCompOffDataError,

    approveRejectCompOffPending,
    approveRejectCompOffRes,
    approveRejectCompOffError,

    applyTourPending,
    applyTourRes,
    applyTourError,

    getTourDataPending,
    getTourDataRes,
    getTourDataError,

    approveRejectTourPending,
    approveRejectTourRes,
    approveRejectTourError,

  } = useSelector(state => state.leave);

  const {
    getAttendanceCorrectionRequestDataPending,
    getAttendanceCorrectionRequestDataRes,
    getAttendanceCorrectionRequestDataError,

    getAttendanceCorrectionApprovalPending,
    getAttendanceCorrectionApprovalRes,
    getAttendanceCorrectionApprovalError,

  } = useSelector(state => state.attendanceCorrection)

  const {
    getAppConstantDataPending,
    getAppConstantDataRes,
    getAppConstantDataError,
  } = useSelector(state => state.common)


  useEffect(() => {
    if (!getEmpApprovelLeavePending) {
      if (getEmpApprovelLeaveError) {
        return;
      }
      if (getEmpApprovelLeaveRes) {
        if (getEmpApprovelLeaveRes?.code === 200) {
          const getLeaveData = getEmpApprovelLeaveRes?.data
          setEmployeeLeaveApprovel(getLeaveData)
        }
      }
    }
  }, [
    getEmpApprovelLeaveRes,
    getEmpApprovelLeaveError,
    getEmpApprovelLeavePending
  ]);

  useEffect(() => {
    if (!getEmpAppliedLeavePending) {
      if (getEmpAppliedLeaveError) {
        return;
      }
      if (getEmpAppliedLeaveRes) {
        if (getEmpAppliedLeaveRes?.code === 200) {
          setEmployeeLeaveRequest(getEmpAppliedLeaveRes?.data)
        }
      }
    }
  }, [
    getLeaveTypeListRes,
    getEmpAppliedLeaveError,
    getEmpAppliedLeavePending
  ]);

  useEffect(() => {
    if (!getLeaveTypeListPending) {
      if (getLeaveTypeListError) {
        return;
      }
      if (getLeaveTypeListRes) {
        if (getLeaveTypeListRes?.code === 200) {
          const getLeaveTypeListResData = getLeaveTypeListRes?.data
          setAllLeaveTypeList(getLeaveTypeListResData)
        }
      }
    }
  }, [
    getLeaveTypeListRes,
    getLeaveTypeListError,
    getLeaveTypeListPending
  ]);
  const tableTabsRequest = [
    { name: "Attendance Correction", onClickFun: (id) => getEmployeeAppliedLeave(id), id: "attendance_correction",permissionId:"attendance_correction_request" },
    // { name: "Web Attendance", onClickFun: (id) => getEmployeeAppliedLeave(id), id: "webattenTabs" },
    { name: "Leaves", onClickFun: (id) => getEmployeeAppliedLeave(id), id: "leave",permissionId:"leave_request" ,permissionId:"leave_request" },
    { name: "Comp-off", onClickFun: (id) => getEmployeeAppliedLeave(id), id: "comp_off",permissionId:"comp_off_request" },
    { name: "Tour", onClickFun: (id) => getEmployeeAppliedLeave(id), id: "tour" ,permissionId:"tour_request"},
  ];
  const tableTabsApproval = [
    { name: "Attendance Correction", onClickFun: (id) => getEmployeeApprovelLeave("attendance_correction"), id: "attendance_correction_approval",permissionId:"attendance_correction_approval" },
    // { name: "Web Attendance", onClickFun: (id) => getEmployeeApprovelLeave(id), id: "webattenTab" },
    { name: "Leaves", onClickFun: (id) => getEmployeeApprovelLeave("leave"), id: "leave_approval" ,permissionId:"leave_approval" },
    { name: "Comp Off", onClickFun: (id) => getEmployeeApprovelLeave("comp_off"), id: "comp_off_approval",permissionId:"comp_off_approval" },
    { name: "Tour", onClickFun: (id) => getEmployeeApprovelLeave("tour"), id: "tour_approval" ,permissionId:"tour_approval"},
  ];
  const activeRequestTab = activeTabPermission(tableTabsRequest)
  const activeApprovalTab = activeTabPermission(tableTabsApproval)



  useEffect(() => {
    handleGetLeaveTypeList()
    // setAppliedType(activeRequestTab)
  }, [])

  useEffect(() => {
    getEmployeeAppliedLeave(appliedType||activeRequestTab, appliedStatus)
  }, [appliedCurrentPage])

  const getEmployeeAppliedLeave = (appliedType, status) => {

    const data = {
      page: appliedCurrentPage,
      limit: pagination.limit,
      // limit: 1,
      request_for: "applied",
      parameter: appliedType,
      status: status === undefined ? "pending" : status
    }

    setProcessType(appliedType)
    if (appliedType === "leave") {
      dispatch(leaveAction.getEmployeeAppliedLeaveRequest(data));
    } else if (appliedType === "attendance_correction") {
      dispatch(attendanceCorrectionActions.getAttendanceCorrectionRequestDataRequest(data))
    }
    else if (appliedType === "comp_off") {
      dispatch(leaveAction.getCompOffDataRequest(data))
    }
    else if (appliedType === "tour") {
      dispatch(leaveAction.getTourDataRequest(data))
    }
  }

  useEffect(() => {
    // if (isApprovalFirstNotRender?.current) {
    //   isApprovalFirstNotRender.current = false;
    //   return;
    // }
    getEmployeeApprovelLeave(approvalType||activeApprovalTab, approvalStatus)
  }, [approvalCurrentPage])

  const getEmployeeApprovelLeave = (type, status) => {
    type =type?.replace(/_approval$/, "").trim()
    const data = {
      page: approvalCurrentPage,
      limit: pagination.limit,
      // limit: 1,
      request_for: "Approval",
      parameter: type,
      status: status === undefined ? "pending" : status
    }
    setProcessType(type)
    if (type === "leave") {
      dispatch(leaveAction.getEmployeeApprovelLeaveRequest(data));
    }
    else if (type === "attendance_correction") {
      dispatch(attendanceCorrectionActions.getAttendanceCorrectionApprovalRequest(data))
    }
    else if (type === "comp_off") {
      dispatch(leaveAction?.approveRejectCompOffRequest(data))
    }
    else if (type === "tour") {
      dispatch(leaveAction?.approveRejectTourRequest(data))
    }
  }

  const handleGetLeaveTypeList = () => {
    dispatch(leaveAction.getLeaveTypeListRequest());
  }


  /*  Attendance Correction  */
  const [dropdownFilterList, setDropdownFilterList] = useState([
    { value: 'pending', label: 'Pending' },
    { value: 'approved', label: 'Approved' },
    { value: 'rejected', label: 'Rejected' },
  ]);
  /* Get Attendance Correction Request Data  */

  useEffect(() => {
    if (!getAttendanceCorrectionRequestDataPending) {
      if (getAttendanceCorrectionRequestDataError) {
        return;
      }
      if (getAttendanceCorrectionRequestDataRes) {
        if (getAttendanceCorrectionRequestDataRes?.code === 200) {
          setAttendanceCorrectionAppliedData(getAttendanceCorrectionRequestDataRes?.data)
        }
      }
    }
  }, [
    getAttendanceCorrectionRequestDataRes,
    getAttendanceCorrectionRequestDataError,
    getAttendanceCorrectionRequestDataPending
  ]);


  useEffect(() => {
    if (!getAttendanceCorrectionApprovalPending) {
      if (getAttendanceCorrectionApprovalError) {
        return;
      }
      if (getAttendanceCorrectionApprovalRes) {
        if (getAttendanceCorrectionApprovalRes?.code === 200) {
          setAttendanceCorrectionApprovalData(getAttendanceCorrectionApprovalRes?.data)
        }
      }
    }
  }, [
    getAttendanceCorrectionApprovalRes,
    getAttendanceCorrectionApprovalError,
    getAttendanceCorrectionApprovalPending
  ]);


  /*Get App Constant Data */
  useEffect(() => {
    getAppConstantData()
  }, [])
  const getAppConstantData = () => {
    if (getAppConstantDataRes && getAppConstantDataRes?.data) {
      setAppConstantData(getAppConstantDataRes?.data)
    } else {
      dispatch(commonActions.getAppConstantDataRequest())
    }
  }
  useEffect(() => {
    if (!getAppConstantDataPending) {
      if (getAppConstantDataError) {
        return;
      }
      if (getAppConstantDataRes) {
        if (getAppConstantDataRes?.code === 200) {
          setAppConstantData(getAppConstantDataRes?.data)
        }
      }
    }
  }, [
    getAppConstantDataRes,
    getAppConstantDataError,
    getAppConstantDataPending
  ]);

  const onHandleProgressModel = (type, id) => {
    if (type || id) {
      const payload = {
        id: id || '',
        type: type || ''
      }
      dispatch(leaveAction.getLeaveProgressRequest(payload))
    }

    setOpenProgressModel(!openProgressModel)

  }
  useEffect(() => {
    if (!getLeaveProgressPending) {
      if (getLeaveProgressError) {
        return;
      }
      if (getLeaveProgressRes) {
        if (getLeaveProgressRes?.code === 200) {
          setProgressData(getLeaveProgressRes?.data)
        }
      }
    }
  }, [
    getLeaveProgressRes,
    getLeaveProgressError,
    getLeaveProgressPending
  ]);


  /* get Comp Off Data */
  useEffect(() => {
    if (!getCompOffDataPending) {
      if (getCompOffDataError) {
        return;
      }
      if (getCompOffDataRes) {
        if (getCompOffDataRes?.code === 200) {
          setCompOffData(getCompOffDataRes?.data)
        }
      }
    }
  }, [
    getCompOffDataRes,
    getCompOffDataError,
    getCompOffDataPending
  ]);

  /* Approve Reject Comp Off */
  useEffect(() => {
    if (!approveRejectCompOffPending) {
      if (approveRejectCompOffError) {
        dispatch(leaveAction.handleClearLeaveStore("HANDLE_CLEAR_APPROVE_REJECT_COMP_OFF_STORE"))
        return;
      }
      if (approveRejectCompOffRes) {
        if (approveRejectCompOffRes?.code === 200) {
          dispatch(leaveAction.handleClearLeaveStore("HANDLE_CLEAR_APPROVE_REJECT_COMP_OFF_STORE"))
        }
      }
    }
  }, [
    approveRejectCompOffRes,
    approveRejectCompOffError,
    approveRejectCompOffPending
  ]);

  /* Apply Tour */
  useEffect(() => {
    if (!applyTourPending) {
      if (applyTourError) {
        dispatch(leaveAction.handleClearLeaveStore("HANDLE_CLEAR_APPLY_TOUR_STORE"))
        return;
      }
      if (applyTourRes) {
        if (applyTourRes?.code === 200) {
          dispatch(leaveAction.handleClearLeaveStore("HANDLE_CLEAR_APPLY_TOUR_STORE"))
        }
      }
    }
  }, [
    applyTourRes,
    applyTourError,
    applyTourPending
  ]);


  /* Get Tour Data */
  useEffect(() => {
    if (!getTourDataPending) {
      if (getTourDataError) {
        return;
      }
      if (getTourDataRes) {
        if (getTourDataRes?.code === 200) {
          setTourData(getTourDataRes?.data)
        }
      }
    }
  }, [
    getTourDataRes,
    getTourDataError,
    getTourDataPending
  ]);


  
  /* Get Tour Data */
  useEffect(() => {
    if (!approveRejectTourPending) {
      if (approveRejectTourError) {
        dispatch(leaveAction.handleClearLeaveStore("HANDLE_CLEAR_APPROVE_REJECT_TOUR_STORE"))
        return;
      }
      if (approveRejectTourRes) {
        if (approveRejectTourRes?.code === 200) {
          dispatch(leaveAction.handleClearLeaveStore("HANDLE_CLEAR_APPROVE_REJECT_TOUR_STORE"))
        }
      }
    }
  }, [
    approveRejectTourRes,
    approveRejectTourError,
    approveRejectTourPending
  ]);


  return (
    <div className="ticketScoreSection">
      <LeaveCountHeader />
      <LeaveApplicationCards
        allLeaveTypeList={allLeaveTypeList}
        attendanceCorrectionAppliedData={attendanceCorrectionAppliedData}
      />
      {isPermission("leave_application_request") &&<ApplicationRequest
        tabletabs={tableTabsRequest}
        tableName='My Request'
        getEmployeeLeaveRequest={getEmployeeLeaveRequest}
        isLoading={getLeaveTypeListPending}
        currentPage={appliedCurrentPage}
        setCurrentPage={setAppliedCurrentPage}
        getEmployeeAppliedLeave={getEmployeeAppliedLeave}
        attendanceCorrectionAppliedData={attendanceCorrectionAppliedData}
        onHandleProgressModel={onHandleProgressModel}
        appliedStatus={appliedStatus}
        setAppliedStatus={setAppliedStatus}
        setAppliedType={setAppliedType}
        compOffData={compOffData}
        tourData={tourData}
        activeRequestTab={activeRequestTab}
      />}
     {isPermission("leave_application_approval") && <ApplicationApproval
        tabletabs={tableTabsApproval}
        tableName="Approval Request"
        getEmployeeLeaveApprovel={getEmployeeLeaveApprovel}
        isLoading={getEmpApprovelLeavePending}
        currentPage={approvalCurrentPage}
        setCurrentPage={setApprovalCurrentPage}
        attendanceCorrectionApprovalData={attendanceCorrectionApprovalData}
        getEmployeeApprovelLeave={getEmployeeApprovelLeave}
        appConstantData={appConstantData}
        onHandleProgressModel={onHandleProgressModel}
        approvalType={approvalType}
        setApprovalType={setApprovalType}
        approvalStatus={approvalStatus}
        setApprovalStatus={setApprovalStatus}
        activeApprovalTab={activeApprovalTab}
      />}
      <ProgressModel
        openModel={openProgressModel}
        handleOpenClose={onHandleProgressModel}
        progressData={progressData}
        processType={processType}
      />
    </div>
  );
};

export default LeaveManagement;
