import React, { useMemo, useState } from 'react'
import { CustomTable } from '../../reuseableComponents'
import { CustCheckbox, Label, Textarea } from '../../commanComponent'
import { checkMaxWordLimit, dateFormate } from '../../../utils/common'
import CustomTooltip from '../../commanComponent/Tooltip'
import { useCustomTranslation } from '../../../translation'
import { CustomButton } from '../../globalComponents'

const MultiApprovalModal = (props) => {
    const { t } = useCustomTranslation()
    const { isOpen,
        handleOpenClose,
        checkedCorrectionData,
        handleCorrectionMultiCheck,
        attendanceCorrectionApprovalData,
        setMultiCheckApprovalReason,
        multiCheckApprovalReason,

    } = props


    const multiSelectFields = [
        { key: 'select', label: '' },
        { key: 'date', label: 'Date' },
        { key: 'name', label: 'Name' },
        { key: 'correction_difference', label: 'Correction Difference' },
        { key: 'check_in_reason', label: 'Reason' },
    ]

    const multiApprovalScopedSlots = useMemo(() => ({
        date: (item, _) => (
            <>{dateFormate(item?.date)}</>
        ),
        select: (item, index) => (
            <>
                <CustCheckbox
                    key={`select_${index}`}
                    id={`select_${index}`}
                    checked={checkedCorrectionData.includes(item)}
                    handleChange={(e) => handleCorrectionMultiCheck(e, item)}
                    name={`select_${index}`}
                />
            </>
        ),
        check_in_reason: (item, _) => (
            <span>{item?.check_in_reason?.split(" ").slice(0, 2).join(' ')}{" "}
                {!(checkMaxWordLimit(item?.check_in_reason, 2)) && <CustomTooltip
                    text={item?.check_in_reason}
                    element={<img
                        src="images/tooltipdots.svg"
                        alt=""
                        width={20}
                    />}
                />}

            </span>
        ),

    }), [checkedCorrectionData]);

    return (
        <>
        <div className={`modal fade attendanceLog ${isOpen ? 'show' : ''}`} id="attenLogs"
            tabIndex="-1"
            style={{ display: isOpen ? 'block' : 'none', }}
            aria-hidden={!isOpen}
        >
            <div className="modal-dialog modal-dialog-centered">
                <div className="modal-content rounded-0 border-0">
                    <div className="modal-header border-0">
                        <h1 className="modal-title fs-5 fw-bold text-primary">
                            Correction Approval
                        </h1>
                        <button
                            type="button"
                            className="btn-close"
                            // data-bs-dismiss="modal"
                            // aria-label="Close"
                            onClick={() => handleOpenClose()}
                        />
                    </div>
                    <div className="modal-body pt-0">
                        <div className="table-responsive tableDiv">
                            <CustomTable
                                data={attendanceCorrectionApprovalData?.data}
                                fields={multiSelectFields}
                                totalPages={1}
                                isHeader={false}
                                tableStyle={{ padding: "0px", boxShadow: "none" }}
                                scopedSlots={multiApprovalScopedSlots}
                            />
                            <Label text={t("labels.reason")} className="text-primary" />
                            <div >

                                <Textarea
                                    value={multiCheckApprovalReason}
                                    placeholder={t("placeholders.reason")}
                                    name="reason"
                                    onChange={(e) => setMultiCheckApprovalReason(e.target.value)}
                                />

                            </div>
                            <div className="my-3 d-flex justify-content-center gap-3">
                                <CustomButton
                                    text={t("btns.rejectAll")}
                                    classNames={"btn-danger px-5 "}
                                    // handleClick={() => checkEncashmentFormula()}
                                    // isLoading={checkLeaveEncashmentFormulaPending}
                                />
                                 <CustomButton
                                    text={t("btns.approveAll")}
                                    classNames={"btn-success px-5"}
                                    // handleClick={() => checkEncashmentFormula()}
                                    // isLoading={checkLeaveEncashmentFormulaPending}
                                />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        {isOpen && <div className="modal-backdrop fade show"></div>}
        </>

    )
}

export default MultiApprovalModal
