import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { leaveAction } from "../../core/resource/actions";
import { toTitleCase } from "../../utils/common";
import { CustomError, CustomSpinner } from "../globalComponents";
import BlankTableError from "../commanComponent/BlankTableError";

const LeaveCountHeader = () => {
  const dispatch = useDispatch()
  const {
    getEmployeeLeavePending,
    getEmployeeLeaveRes,
    getEmployeeLeaveError,

  } = useSelector(state => state.leave)
  const [empLeaveData, setEmpLeaveData] = useState()

  useEffect(() => {
    getEmployeeLeaves()
  }, [])

  const getEmployeeLeaves = () => {
    dispatch(leaveAction.getEmployeeLeaveRequest())
  }

  useEffect(() => {
    if (!getEmployeeLeavePending) {
      if (getEmployeeLeaveError) {
        return;
      }
      if (getEmployeeLeaveRes) {
        if (getEmployeeLeaveRes?.code === 200) {
          setEmpLeaveData(getEmployeeLeaveRes?.data)
        }
      }
    }
  }, [
    getEmployeeLeaveRes,
    getEmployeeLeaveError,
    getEmployeeLeavePending
  ]);

  const renderEmployeeLeaves = () => {
    return (
      empLeaveData&& empLeaveData?.length?empLeaveData?.map((item, ind) => (
        <div className="col-md-3 px-md-2 mb-3" key={ind}>
          <div className="totalBx h-100">
            <figure className="mb-0">
              <img src="images/applyleavesIcon.svg" alt="" width={40} />
            </figure>
            <div className="d-flex justify-content-between align-items-center">
              <h3 className="fw-bolder mb-0 mt-3">{toTitleCase(item?.leave_type)} </h3>
              <span>{item?.leave_balance}</span>
            </div>
            <img
              src="images/totalattendance.svg"
              className="cornerLine"
              alt=""
              width={95}
            />
          </div>
        </div>
      )):(
        <div className='d-flex justify-content-center'
        style={{
          fontSize: "20px",
          padding: "18px"
        }}
        > <CustomError  text={"Leave Not Available"}/></div>
    )
    )
  }
  return (
    <>
      <div className="cardBox mb-3 leaveAppli" >
        <div className="row px-md-1">
          {getEmployeeLeavePending ?
            <div>
              <CustomSpinner isShow={getEmployeeLeavePending} size="xl" />
            </div> : renderEmployeeLeaves()}
          {/* <div className="col-md-3 px-md-2 mb-3">
            <div className="totalBx h-100">
              <figure className="mb-0">
                <img src="images/applyleavesIcon.svg" alt="" width={40} />
              </figure>
              <div className="d-flex justify-content-between align-items-center">
                <h3 className="fw-bolder mb-0 mt-3">Comp-off </h3>
                <span>01</span>
              </div>
              <img
                src="images/coff-line.svg"
                className="cornerLine"
                alt=""
                width={50}
              />
            </div>
          </div>
          <div className="col-md-3 px-md-2 mb-3">
            <div className="totalBx h-100">
              <figure className="mb-0">
                <img src="images/applyleavesIcon.svg" alt="" width={40} />
              </figure>
              <div className="d-flex justify-content-between align-items-center">
                <h3 className="fw-bolder mb-0 mt-3">Leave Available </h3>
                <span>08</span>
              </div>
              <img
                src="images/leaveavailable-line.svg"
                className="cornerLine"
                alt=""
                width={80}
              />
            </div>
          </div>
          <div className="col-md-3 px-md-2 mb-3">
            <div className="totalBx h-100">
              <figure className="mb-0">
                <img src="images/applyleavesIcon.svg" alt="" width={40} />
              </figure>
              <div className="d-flex justify-content-between align-items-center">
                <h3 className="fw-bolder mb-0 mt-3">Leave Taken </h3>
                <span>03</span>
              </div>
              <img
                src="images/leavetaken-line.svg"
                className="cornerLine"
                alt=""
                width={60}
              />
            </div>
          </div> */}

        </div>
      </div>
    </>
  );
};

export default LeaveCountHeader;
