import React, { useState, useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { leaveAction } from '../../../core/resource/actions';
import { toast } from 'react-toastify';
import { CustomButton } from '../../globalComponents';

const LeaveApprovalModel = ({ openApprovalModel, setOpenApprovalModel, approvalData, getEmployeeAppliedLeave, setApproveData }) => {
    const dispatch = useDispatch()
    const { approveRejectLeavePending,
        approveRejectLeaveRes,
        approveRejectLeaveError } = useSelector(state => state.leave)
    const [addApproval, setAddprroval] = useState({
        _id: '',
        comment: "",
        action_type: ''
    })

    useEffect(() => {
        if (!approveRejectLeavePending) {
            if (approveRejectLeaveError) {
                toast.error(approveRejectLeaveError?.errorMessage)
                return;
            }
            if (approveRejectLeaveRes) {
                if (approveRejectLeaveRes?.code === 200) {
                    resetFunc()
                    getEmployeeAppliedLeave('leave')
                    dispatch(leaveAction.handleClearLeaveStore("HANDLE_APPROVE_REJECT_LEAVE_STORE"))
                }
            }
        }
    }, [
        approveRejectLeaveRes,
        approveRejectLeaveError,
        approveRejectLeavePending
    ]);

    const handleFunction = (event) => {
        const { name, value } = event.target;
        setAddprroval({ ...addApproval, [name]: value });

    }

    const handleApproval = (data) => {
        addApproval["action_type"] = data
        dispatch(leaveAction.approveRejectLeaveRequest(addApproval))
    }

    const resetFunc = () => {
        setOpenApprovalModel(!openApprovalModel)
        setApproveData([])
        setAddprroval({
            _id: '',
            comment: '',
            action_type: ''
        })
    }

    return (
        <div className={`modal fade attendanceLog ${openApprovalModel ? 'show' : ''}`}
            tabIndex="-1"
            style={{ display: openApprovalModel ? 'block' : 'none', }}
            aria-hidden={!openApprovalModel} >
            <div className="modal-dialog modal-dialog-centered" style={{ maxWidth: '700px' }}>
                <div className="modal-content rounded-0 border-0">
                    <div className="modal-header border-0">
                        <h1 className="modal-title fs-5 fw-bold text-primary">Leave Requests</h1>
                        <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"
                            onClick={() => resetFunc()}
                        />
                    </div>
                    <div className="modal-body pt-0">
                        <div className="table-responsive tableDiv">
                            <table className="table">
                                <thead>
                                    <tr>
                                        <th width="10%">
                                            <div className="form-check m-0">
                                                {/* <input className="form-check-input" type="checkbox" value="" id="LeaveCheck1"/> */}
                                                <label className="form-check-label ps-2" for="LeaveCheck1">
                                                </label>
                                            </div>
                                        </th>
                                        <th>Sr. No</th>
                                        <th>Name </th>
                                        <th>Leave Date</th>
                                        <th>Leave Type</th>
                                        <th>Leave Status</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {approvalData && approvalData.map(item => (
                                        <tr>
                                            <td>
                                                <div className="form-check">
                                                    <input className="form-check-input" type="checkbox" value={item.approvel_id} name='_id' id="LeaveCheck2" onChange={(e) => handleFunction(e)} />
                                                    <label className="form-check-label" for="LeaveCheck2">
                                                    </label>
                                                </div>
                                            </td>
                                            <td><span className="srNum d-flex m-0">1</span></td>
                                            <td>{item.first_name}</td>
                                            <td>{item.leave_date}</td>
                                            <td>{item.leave_short_name}</td>
                                            <td>{item.leave_status}</td>
                                        </tr>

                                    ))}


                                </tbody>
                            </table>
                            <span>Enter Reason</span>
                            <div className="form-group mb-3">
                                <textarea className="form-control" placeholder="Enter Reason" value={addApproval?.comment} name='comment' onChange={(e) => handleFunction(e)} style={{ height: "70px" }}></textarea>
                            </div>
                            <div className="my-3 d-flex justify-content-center gap-3">
                                <span className="btn-danger px-4" style={{ cursor: "pointer" }} onClick={() => handleApproval("reject")}>Reject</span>
                            
                                <span className="btn-success" style={{ cursor: "pointer" }} onClick={() => handleApproval("approve")}>Approve</span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default LeaveApprovalModel