import React from "react";
import { isPermission } from "../../utils/common";

const TableTabs = ({ tabletabs, type }) => {
  const visibleTabs = tabletabs.filter(
    (tab) => !tab?.permissionId || isPermission(tab.permissionId)
  );

  return (
    <ul className="nav nav-tabs leaveTabs mb-4">
      {visibleTabs.map((tab, index) => (
        <li className="nav-item" key={tab.id || index}>
          <button
            className={`nav-link ${index === 0 ? "active" : ""}`}
            data-bs-toggle="tab"
            data-bs-target={`#${tab.id}`}
            type="button"
            onClick={() => tab.onClickFun(tab.id, type)}
          >
            {tab.name}
          </button>
        </li>
      ))}
    </ul>
  );
};

export default TableTabs;
